import React, { Component, createRef, Suspense } from 'react';
import { connect } from 'react-redux';
import { submitPostData, onSitePopup, onsiteTokenUpdate } from '../../../../actions/themeActions'
import Rating from './Components/Rating';
import { validateEmail } from '../../../../utils';
import './review-hub.scss'
import { CloudUrl, SocialWallServerTestUrl, SocialWallServerUrl } from '../../../../constants';
import { WebUrl } from '../../../../constants';
import { AI_REVIEW, isSocialWall, isTagbox, isTagEmbed, isTagShop, socket_client_url,FOR_TEST_SERVER, onsiteRedirectUrl } from '../../../../actions/api';
import { AiIcon, AiIconColored } from '../OnSiteUpload/writeCaption';
import axios from 'axios';
import io from "socket.io-client";
const Uploads = React.lazy(() => import('./Components/Uploads'));
  
const Alert = ({ isPhotoBooth, wall, type, onClosePopUp, onCloseUserPopup }) => {
    return <div className="t_modal_content">
        <div className='t_s_wrap'>
            <img className='t_s_icon_' src={`${CloudUrl}/media/images/${type}-icon.svg`} height={80} width={80} alt='' />
            {type == "check" ? <div className="t_s_title">Thanks for Your Efforts!</div> : null}
            <div className="t_s_desc">{isPhotoBooth ? wall.PhotoBoothSettings.photo_booth_succ_msg : wall.UgcSettings.onsite_status ? wall.UgcSettings.onsite_succ_msg : `Thank you for sharing your amazing content with us.`}</div>
            <div className='t_f_close_wrap'>
                <button className='t_f_p_btn' onClick={[179126].includes(wall?.Wall?.owner) ? onCloseUserPopup : onClosePopUp}>Close</button>
            </div>
        </div>
    </div>
}

const Logo = ({ logo }) => {
  if (!logo) return null;
    return (
        <div className="t_m_logo_w">
        <img
            alt=""
            className="t_m_logo"
            height={20}
            src={logo}
            width={130}
        />
        </div>
    )}

class ReviewHub extends Component {


    constructor(props) {
        super(props);
        this.state = {
            submitPost: true,
            activeTab: 1,
            isOpen: true,
            step: 1,
            wall_id: null,
            name: '',
            email: '',
            upload_image: [],
            receiveMail: false,
            tandc: true,
            nameError: false,
            emailError: false,
            submitErrorMessage: null,
            apiRequest: false,
            isActiveIndex: 0,
            isAddMoreActive: false,
            isEdit: false,
            submitError: false,
            is_terms_check: 0,
            errorClickMore: false,
            uploadError: '',
            rateProduct: 0,
            reviewTitle: '',
            review: '',
            rateProductError: false,
            reviewTitleError: false,
            captionError: false,
            layout: props?.wall?.UgcSettings?.choose_content_category || 1,
            removedFile:{},
            formLanguage : null,
            aiInput: false,
            aiPrompt: '',
            aiPromptError: '',
            aiPromptCopy: '',
            writeAiReview: false,
            aiReadOnly: true,
            aiSpinner: false,
            isRegenrate: false,
            fileUpload : false
        }
        this.textAreaRef = createRef();
    }

    handleNext = event => this.setState({ submitPost: false })
    hanleTab1 = event => this.setState({ activeTab: 1 })
    hanleTab2 = event => this.setState({ activeTab: 2 })

    checkUpload=(e)=>  this.setState({ fileUpload: e })

    componentDidMount() {
        const { wall,isPhotoBooth } = this.props;
        this.setState({ activeTab: wall.UgcSettings.choose_content_category == 3 ? 2 : 1 })
            // Fetch the JSON file
            fetch('https://cloud.socialwalls.com/react-app/media/custom_lang.json')
            .then(response => response.json())  // Parse the response as JSON
            .then(data => {
               this.setState({ formLanguage:  data[isPhotoBooth ? wall?.PhotoBoothSettings?.photo_booth_language : wall?.UgcSettings?.onsite_language] });  // Update state with the JSON data
            })
            .catch(error => {
            console.error('Error fetching JSON file:', error);
            });
    }

    onShowCloseSubmitPicPopUp = event => {
        const { isOpen } = this.state;
        this.setState({ isOpen: !isOpen })
    }


    onSubmitPostData = event => {
        if (event) {
            this.setState({
                submitError: true,
                submitErrorMessage: "Please wait until the file is uploaded before submitting."
            });
            return;
        }
        
        const { name, email, upload_image, is_terms_check, tandc, rateProduct, reviewTitle, review, layout, captionError, emailError } = this.state;
        const { wallId, modalPop, wall, feeds, isPhotoBooth } = this.props;
        this.setState({ submitError: false })
        if (tandc && (name?.length > 0) && (email?.length > 0 && validateEmail(email) && !email.includes("mailinator") || ([179935].includes(wall?.Wall?.owner) && !emailError) || ([179126].includes(wall?.Wall?.owner) && !emailError)) && tandc && ((layout == 1 && rateProduct > 0 && (isTagShop ? reviewTitle : true)) || layout == 2) && review?.length > 0 && !captionError) {
            this.setState({ apiRequest: true })
            var ugcSuite = window.ugcSuite ? true : window.location.href.includes("ugcSuite=1") ? true : false
            var feedData = {}
            if (ugcSuite) {
                if (feeds && feeds.length > 0) {
                    feeds.map((fItem) => {
                        if (fItem && fItem.Network && Object.keys(fItem.Network).length > 0 && fItem.Network.id == 29 && fItem.value1) {
                            feedData = { feedId: fItem.value1 }
                        }
                    })
                }
            }
            let upload_files = []
            if (upload_image && upload_image.length > 0) {
                upload_image.map((itmFile) => upload_files.push({ ...itmFile }))
            }
            else {
                upload_files = [{
                    file: "",
                    type: 1,
                    height: "",
                    width: "",
                    thumbnail: "",
                }]
            }

            const postData = { wallId: wall?.Wall?.wallId ?? wallId, name, email, media: !isPhotoBooth ? upload_files : [], onsite_token: modalPop.onsite_token, ...feedData, caption: review, rating: rateProduct, title: layout == 2 ? " " : reviewTitle, mediaBuffer : isPhotoBooth || "" }

            if ([104746].includes(wall.Wall.owner)) {
                postData.is_terms_check = is_terms_check
            }

            submitPostData(postData).then((response) => {
                const { onsite_token, message } = response.data;
                this.props.onsiteTokenUpdate(onsite_token)  
                const isModerationDisabled = isPhotoBooth ? wall?.PhotoBoothSettings?.onsite_moderation : wall?.UgcSettings?.onsite_moderation;

                if (!window.location.href.includes('studio=1') && !isModerationDisabled  && message) {
 
                    const SOCKET_CONNECTION = io(`${socket_client_url}`, {
                        transports: ["websocket"],
                        transport: ["websocket"],
                        upgrade: true,
                        reconnection: true,
                        reconnectionDelay: 3000,
                        reconnectionDelayMax: 5000,
                        reconnectionAttempts: 2
                    });


                    var notify = new Array();
                    notify.push(wallId);
                    notify.push(message[0].synced);
                    //console.log(notify,'notifynotifynotifynotify')
                    SOCKET_CONNECTION.emit("poststatus", notify);
                    SOCKET_CONNECTION.on("reconnect_attempt", (attemptNumber) => {
                       // console.log(`Reconnect attempt #${attemptNumber}`);
                        if (attemptNumber > 2) {
                            SOCKET_CONNECTION.disconnect(); // Stop reconnecting after 2 attempts
                        }
                    });
                }

                 
                this.setState({ step: 3, apiRequest: false }, () => setTimeout(() => {
                    if (window.location.href.includes("onsite-upload")) window.location.href =`${onsiteRedirectUrl}${wallId}?social-wall=true`
                    else this.props.onSitePopup(false)
                }, 5000), document.querySelector('html').style.overflow = "auto")
            })

                .catch((error) => {
                    const { data } = error.response;
                    if (data) {
                        this.setState({ submitError: true, submitErrorMessage: data.resposne_message, apiRequest: false })
                    }
                    console.error(error);
                });
        }
        else {
            if (this.state.review == '' || this.state.review.trim().length == 0) this.setState({ captionError: true, apiRequest: false })
            if (this.state.name == null || this.state.name.trim().length == 0) this.setState({ nameError: true, apiRequest: false })
            if (this.state.email == null || this.state.email.trim().length == 0 || !validateEmail(this.state.email) || email.includes("mailinator")) if(![179935].includes(wall?.Wall?.owner)) this.setState({ emailError: true, apiRequest: false })
            if (rateProduct == 0) this.setState({ rateProductError: true })
            if (this.state.reviewTitle == null || this.state.reviewTitle.trim().length == 0) this.setState({ reviewTitleError: true, apiRequest: false })
        }
    }


    addCaption = event => {
        if (String(event.target.value).length <= 500) this.setState({ review: event.target.value, captionError: false })
        else this.setState({ captionError: true, review: event.target.value })
    }
   

    addRatings = (rating) => this.setState({ rateProduct: rating, rateProductError: false })

    onChangeReviewTitle = event => {
        this.setState({ reviewTitle: event.target.value, reviewTitleError: false })
    }


    addPostImage = (file, thumb_file, videoPath, type, activeIndex, ext, size, fileName, mediaHeight, mediaWidth) => {

        let { upload_image } = this.state;
        const uploadDetails = {
            file: file,
            type: type,
            height: mediaHeight,
            width: mediaWidth,
            thumbnail: thumb_file
        }
        upload_image.push(uploadDetails);
        if(upload_image.length > 20) upload_image.length = 20
        this.setState({ upload_image })
        this.setState({fileUpload: false, submitError: false })
    }

    onClickToRemove = (item,activeIndex) =>  {
        const { upload_image } = this.state;
        if (upload_image && upload_image.length > 0) {
            const newImages = upload_image.filter(file => file.file != item.file)
            this.setState({ upload_image: newImages, errorClickMore: false,  removedFile:item })
        }
    }


    onClickToEdit = (activeIndex) => event => {
        const { upload_image } = this.state;
        let isActiveTab = 1;
        if (upload_image && upload_image[activeIndex]) {
            isActiveTab = upload_image[activeIndex].activeTab;
        }
        this.setState({ isEdit: true, step: 1, activeTab: isActiveTab })
    }

    onTextName = event => {
        this.setState({ name: event.target.value, nameError: false }, () => {
            if (this.state.name == null || this.state.name.length == 0) this.setState({ nameError: true })
        })
    }
   
    onTextEmail = (event) => {
        const { value } = event.target;
        const { wall } = this.props;
        this.setState({ email: value, emailError: false }, () => {
          const { email } = this.state;
          const isInvalid = event.type == 'blur'
            ? !validateEmail(email) || email.includes('mailinator')
            : !email;
            if(!value && [179935].includes(wall?.Wall?.owner)){
                this.setState({ emailError: false });
            }else if (isInvalid) this.setState({ emailError: true });
        });
      };
      

    onUpdatereceiveMail = event => {
        const { receiveMail } = this.state;
        this.setState({ receiveMail: !receiveMail })
    }
    onUpdatetandc = event => {
        const { tandc } = this.state;
        this.setState({ tandc: !tandc })
    }

    onCloseUserPopup = () => {
        const { wallId, wall } = this.props;
        // Reset overflow
        document.querySelector('html').style.overflow = "auto";
        // Close popup
        this.props.onSitePopup(false);
        // Extract wallKey from URL
        const wallKey = new URLSearchParams(window.location.search).get('wallKey');
        // Determine base URL
        const baseUrl = FOR_TEST_SERVER 
            ? `${SocialWallServerTestUrl}/${wallId}` 
            : `${SocialWallServerUrl}/${wallId}`;
    
        // Add wallKey if it exists
        const finalUrl = wallKey ? `${baseUrl}?wallKey=${wallKey}` : baseUrl;
        // Redirect
        window.location.href = [179126].includes(wall?.Wall?.owner) ? 'https://www.dogparkadventures.com/thank-you/' : finalUrl;
    }





     onClosePopUp = () => {
        const { wallId } = this.props;
        // Reset overflow
        document.querySelector('html').style.overflow = "auto";
        // Close popup
        this.props.onSitePopup(false);
        // Extract wallKey from URL
        const wallKey = new URLSearchParams(window.location.search).get('wallKey');
        // Determine base URL
        const baseUrl = FOR_TEST_SERVER 
            ? `${SocialWallServerTestUrl}/${wallId}` 
            : `${SocialWallServerUrl}/${wallId}`;
    
        // Add wallKey if it exists
        const finalUrl = wallKey ? `${baseUrl}?wallKey=${wallKey}` : baseUrl;
        // Redirect
        window.location.href = finalUrl;
    };

    
    termsCheck = (event) => {
        this.setState({ is_terms_check: event })
    }

    uploadErrorCallback = (error) => {
        
        this.setState({
            uploadError: error, fileUpload: false
        }, () => {
            setTimeout(() => {
                this.setState({ uploadError: '' })
            }, 5000)
        })
    }

    backStepFun = () => {
        this.setState({
            step: 1
        })
    }

    
    aiPromptFun = event => {
        if (String(this.state.aiPromptCopy).trim() != '' && String(this.state.aiPromptCopy).trim() != String(event.target.value).trim()) this.setState({ isRegenrate: true })
        this.setState({ aiPrompt: event.target.value })
 }


 typeWriterEffect = () => {
    const { layout } = this.props
    const { aiResponse, currentIndex } = this.state;
    const speed = 20; // Adjust typing speed (milliseconds)

    if (currentIndex < aiResponse.length) {
           if (layout == 1) {
                  this.setState(prevState => ({
                         currentIndex: prevState.currentIndex + 1,
                         review: `${prevState.review}${aiResponse[currentIndex]}`
                  }), () => {
                         setTimeout(this.typeWriterEffect, speed);
                  });
           }
           else {
                  this.setState(prevState => ({
                         currentIndex: prevState.currentIndex + 1,
                         review: `${prevState.review}${aiResponse[currentIndex]}`
                  }), () => {
                         setTimeout(this.typeWriterEffect, speed);
                  });
             }

        } else {
           this.setState({
                  isTyping: false,
                  loadbtn: false
           });
    }
}


 aiReviewFun = (event) => {
    const { aiPrompt, rateProduct, layout } = this.state;
    // Update initial state
    this.setState({
           aiInput: true, // Set aiInput to true if event exists
           aiReadOnly: true, // Make input read-only
           isTyping: true, // Indicate typing is in progress
           currentIndex: 0, // Reset current index for typewriter effect
           review: '', // Clear previous review
           aiPromptError: ''
    }); 
 
    const words = aiPrompt.split(/\s+/);
    const wordLengths = words.map(word => word.length);

    // Check if aiPrompt has enough words and rateProduct is valid
    if (!aiPrompt || wordLengths.length <= 2) {
           this.setState({ aiPromptError: 'Please enter at least 3 words.' });
           return false;
    }

    // Start the AI review process
    this.setState({
           writeAiReview: true,
           loadbtn: true,
           aiSpinner: true,
           isRegenrate: false,
           aiResponse: "",
           rateProductError: false
           // aiPromptError:''
    });

        // Make API request 
        axios.post(AI_REVIEW, { input_text: aiPrompt, stars: layout == 2 ? rateProduct : 0 })
            .then((response) => {
                    if (response.data.status_code == 200) {
                            this.setState({
                                    aiResponse: response.data.body,
                                    item :  response.data.body,
                                    aiSpinner: false,
                                    aiPromptCopy: aiPrompt,
                                    isRegenrate: true,
                                    captionError: false
                            }, () => {
                                    // Start typewriter effect after setting the response
                                    this.typeWriterEffect();
                            });
                    } else {
                            this.setState({
                                    loading: false,
                                    isTyping: false,
                                    loadbtn: false,
                                    aiSpinner: false,
                                    aiPromptError: response.data.body,
                                    aiPromptCopy: aiPrompt,
                                    isRegenrate: false,
                                    captionError: false
                            })
                    }
            })
            .catch((error) => {
                    this.setState({
                            error: error.message,
                            loading: false,
                            isTyping: false,
                            loadbtn: false,
                            aiSpinner: false,
                            aiPromptError: 'Internal Server Error',
                            aiPromptCopy: aiPrompt,
                            isRegenrate: false,
                            captionError: false
                    });
              });
      }


        aiPromptEditFun = () => {
            this.setState({ aiReadOnly: false }, () => {
                if (this.textAreaRef.current) {
                        const textArea = this.textAreaRef.current;
                        textArea.focus();
                        textArea.setSelectionRange(textArea.value.length, textArea.value.length); // Focus on textarea when edit button is clicked
                }
            })
        }


    WriteWithAI = event => {
        try {
            this.setState({ aiInput: true })
        }
        catch (error) {
            console.log("WriteWithAI error", error)
        }
    }

    closeAIPrompt = event => {
        try {
              this.setState({ aiInput: false, aiPromptError: '' })
         }
        catch (error) {
               console.log("closeAIPrompt", error)
        }
 }
 closeInputAIPrompt = event => {
        try {
             this.setState({ writeAiReview: false, aiPrompt: '', review: '', aiPromptError: '', aiInput: false, isRegenrate: false })
          }
        catch (error) {
               console.log("closeInputAIPrompt", error)
        }
 }

    render() {
        const { aiSpinner,aiReadOnly, aiPromptError, isActiveIndex, step,aiInput, aiPrompt, upload_image, formLanguage, writeAiReview, name, email, isRegenrate, tandc, nameError, emailError, apiRequest, submitError, submitErrorMessage, errorClickMore, uploadError, reviewTitle, review, rateProductError, reviewTitleError, captionError, layout, removedFile } = this.state;
        const { wall, modalPop, isFreeAdsStatus, isPhotoBooth, photoBoothInterface } = this.props;

        const backgroundColor = isPhotoBooth  ? wall?.PhotoBoothSettings?.photo_booth_bg_color : wall?.UgcSettings?.onsite_background_color || null;
        const backgroundImage = `url(${isPhotoBooth ? wall?.PhotoBoothSettings?.photo_booth_bg_image : wall.UgcSettings.onsite_background_image})`;
        const backgroundStyle = { backgroundColor, backgroundImage };

        const hasUgcStatus = wall?.UgcSettings && Object.keys(wall.UgcSettings).length > 0 && wall.UgcSettings.onsite_status;
        const hasPhotoBoothStatus = wall?.PhotoBoothSettings && Object.keys(wall.PhotoBoothSettings).length > 0 && wall.PhotoBoothSettings.photo_booth_status;
        const popup_title = isPhotoBooth ? wall?.PhotoBoothSettings?.photo_booth_popup_title || 'Almost There!' : wall?.UgcSettings?.onsite_popup_title;
        const sub_heading = isPhotoBooth ? wall?.PhotoBoothSettings?.photo_booth_popup_sub_heading || 'Add a few details below, and your photo will go live'  : wall?.UgcSettings?.onsite_popup_sub_heading;

        const { onClosePopUp, onCloseUserPopup } = this;
        return <div className={`${!photoBoothInterface ? 't_modal' : 'photobooth_form_top' } ${isFreeAdsStatus ? ' t_brand_' : ''}`}>
            {/* <div className='t_overlay_'><div></div></div> */}
            <div className={`${!photoBoothInterface ? 't_modal_wrap' : 'photobooth_form' }  style={${backgroundStyle}}`}>
                {(wall && (hasUgcStatus || hasPhotoBoothStatus)) ?
                    <div className={`${!photoBoothInterface ? 't_modal_container' : '' }`}>
                        {step == 3 ?
                            <Alert isPhotoBooth={isPhotoBooth} wall={wall} onClosePopUp={onClosePopUp} onCloseUserPopup={onCloseUserPopup} type={"check"} />
                            :
                            <div className={`${!photoBoothInterface ? 't_modal_content' : '' }`}>
                                <Logo logo={isPhotoBooth ? wall.PhotoBoothSettings.photo_booth_logo : wall.UgcSettings.onsite_logo}/>
                                { !photoBoothInterface ? <div className="t_m_close_btn tb__icon tb-close-alt" onClick={onClosePopUp}><div></div></div> : null}
                                {popup_title ? <div className='t_m_title'>{popup_title}</div> : null}
                                {sub_heading != '' ? <div className='t_m_subtitle'>{sub_heading}</div> : null}
                                {layout == 1 && wall.UgcSettings.choose_content_category==1 ? <div className='t_f_group'>
                                    <Rating item={upload_image[isActiveIndex]} addRatings={this.addRatings} />
                                    {rateProductError ? <div className='t_f_error t_text_center'> * Rating is required.</div> : null}
                                </div> : null}
                                {layout == 1 && !isSocialWall && !isTagEmbed && !isTagbox? <div className='t_f_group'>
                                    <div className='t_label_'> {'Review Title' }  <div className='t_t_danger'>*</div></div>
                                    <input type="text" className={`t_from_input ${reviewTitleError ? 't_f_invalid' : ''}`} placeholder={`Enter review title`} onChange={this.onChangeReviewTitle} value={reviewTitle} />
                                    {reviewTitleError ? <div className='t_f_error'> * Review Title is required.</div> : null}
                                </div> : null} 

                               { isPhotoBooth ?     
                                <div style={{ textAlign: 'center', marginBottom: '10px' }}>    
                                   <img src={isPhotoBooth} className='t_m_bg' alt='' style={{ width: '100px', height: '100px', margin : '0 auto' }} />
                                </div> : null } 

                                <div className='t_f_group'>
                                    <div className='t_label_'>{ (wall.UgcSettings.choose_content_category==1 ? formLanguage?.Review : [179126].includes(wall?.Wall?.owner) ? 'TELL US ABOUT YOUR ADVENTURE, LOCATION and DOGS NAMES' : formLanguage?.UGC)  || (isPhotoBooth ? 'Add a caption' : layout == 2 ? `Caption` : `Review`)}  <div className='t_t_danger'>*</div></div>
                                    
                                    {!writeAiReview ? <div className='t_f_ai_res_wrap'>

                                    {!aiInput ?
                                    <textarea className={`t_from_textarea aaa${captionError ? 't_f_invalid' : ''}`} placeholder={(wall.UgcSettings.choose_content_category==1 ? formLanguage?.Write_review : formLanguage?.Write_caption)  ||  `Write your ${layout == 2 ? `caption` : `review`} here`}
                                        onChange={this.addCaption}>{review}</textarea>     
                                      :
                                      <textarea className={`t_from_textarea t_f_ai_prompt`} value={aiPrompt} onChange={this.aiPromptFun} placeholder={layout == 1 ? `Describe what your caption should be about.` : `Provide a brief of the product or service for the review.`} />
                                     }  
                                    
                                    {aiInput ? <div className='t_f_ai_ico_text'><AiIconColored /></div> : ''}
                                    {aiInput ? <div className='t_f_ai_btn_wrap'><div className='t_f_ai_btn' onClick={this.closeAIPrompt}><div className='tb__icon tb-close-alt'></div></div></div> : ''}

                                    {aiInput ?
                                            <div className={`t_f_p_btn t_f_p_btn_gr t_f_ai_btn${aiPrompt.length <= 0 && aiInput ? ' tb_disabled' : ''}`} onClick={this.aiReviewFun}>
                                                <div className='t_f_ai_ico_btn'><AiIcon /></div>Generate
                                            </div>
                                            :
                                            <div className={`t_f_p_btn t_f_p_btn_gr t_f_ai_btn${aiPrompt.length <= 0 && aiInput ? ' tb_disabled' : ''}`} onClick={this.WriteWithAI}>
                                                <div className='t_f_ai_ico_btn'><AiIcon /></div> Write with AI
                                            </div>
                                     }

                                   </div>  : 
                                   
                                        <div className='t_f_textarea ai_input_wrap'>
                                            <div className='t_f_ai_input_wrap_'>
                                                    <div className='t_f_ai_ico'><AiIconColored /></div>
                                                    <textarea id='ai_input' ref={this.textAreaRef} className={`t_from_ai_input t_f_textarea`} value={aiPrompt} onChange={this.aiPromptFun} placeholder={`Write Ai Review`} readOnly={aiReadOnly} disabled={aiReadOnly} />
                                                    <div className='t_f_ai_btn_wrap'>
                                                        <div className='t_f_ai_btn' onClick={this.aiPromptEditFun}><div className='tb__icon tb-edit'></div></div>
                                                        <div className='t_f_ai_btn' onClick={this.closeInputAIPrompt}><div className='tb__icon tb-close-alt'></div></div>
                                                    </div>
                                            </div>
                                          
                                            <div className='t_f_ai_res_wrap'>
                                                    {aiSpinner ?
                                                        <div className='t_f_skel_wrap'>
                                                                <div class="t_f_s_bar_wrap"><div class="t_f_s_bar"><div></div></div></div>
                                                                <div class="t_f_s_bar_wrap"><div class="t_f_s_bar"><div></div></div></div>
                                                                <div class="t_f_s_bar_wrap" style={{ maxWidth: '50%' }}><div class="t_f_s_bar"><div></div></div></div>
                                                        </div>
                                                        : null}
                                                    <textarea className={`t_from_textarea bbb${captionError ? 't_f_invalid' : ''}`} value={review} onChange={this.addCaption } />
                                                    <div className={`t_f_p_btn t_f_rg_btn t_f_l_btn${!isRegenrate ? ' tb_disabled' : ''}`} onClick={() => isRegenrate ? this.aiReviewFun(false) : false}>Regenerate</div>
                                            </div>
                                      </div>
                                   } 

                                  {aiPromptError ? <div className='t_f_error'> * {aiPromptError}</div> : null}
                                  {captionError ? <div className='t_f_error'> * {String(review).length <= 0 ? `${layout == 1 ? `Review` : `Caption`} is required.` : `${layout == 1 ? `Review` : `Caption`} limit of 500 exceeded!`}</div> : null}

                              </div>

                              { !isPhotoBooth  ? 
                                <div className='t_f_group'>
                                    { !isSocialWall ?<div className='t_label_'>Picture/Video (optional)</div> : null }
                                    <div className='t_f_upload'>
                                        {(upload_image && upload_image.length > 0) ?
                                            upload_image && upload_image.map((item, index) => item.thumbnail ? <div key={index} className='t_m_up_img_wrap' onClick={this.onClickToEdit(index)}>
                                                {item.thumbnail ?
                                                    <div className='t_m_up_img_in'>
                                                        <img className='t_m_up_img' src={item.thumbnail} height={80} width={80} alt='' />
                                                        {item.type == 3 || item.type == 5 ? <div className="t_m_vi_ico tb__icon tb-play"><div></div></div> : null}
                                                    </div>
                                                    : null}
                                                <div className="t_m_remove_btn tb__icon tb-close-alt" onClick={()=>this.onClickToRemove(item,index)}><div></div></div>
                                            </div> : null)
                                            : null}
                                        <Suspense fallback={<div className='t_m_upload'>Loading...</div>}>
                                            <Uploads checkUpload={this.checkUpload} formLanguage={formLanguage} isActiveIndex={isActiveIndex} addPostImage={this.addPostImage} onsite_token={modalPop.onsite_token} uploadErrorCallback={this.uploadErrorCallback} upload_image={upload_image} removedFile={removedFile}/>
                                        </Suspense>
                                    </div>
                                    {/* <div className='t_info_'>Video Size : Max 100MB | Photo Size : Max 15MB | Max 20</div> */}
                                    {uploadError ? <div className='t_f_error'>{uploadError}</div> : null}

                                </div> : null }


                                <div className='t_row_'>
                                    <div className='t_col_'>
                                        <div className='t_f_group'>
                                            <div className='t_label_'> {formLanguage?.Name ?? `Your Name`}  <div className='t_t_danger'>*</div></div>
                                            <input type="text" className={`t_from_input ${nameError ? 't_f_invalid' : ''}`} placeholder={ formLanguage?.Enter_Name ?? `Full Name`} onChange={this.onTextName} value={name} />
                                            {nameError ? <div className='t_f_error'> Name is required.</div> : null}
                                        </div>
                                    </div>
                                    {![179126].includes(wall.Wall.owner) ? <div className='t_col_'>
                                        <div className='t_f_group'>
                                            <div className='t_label_'> {formLanguage?.Email ?? `Your Email`}   <div className='t_t_danger'>{![179935].includes(wall?.Wall?.owner) ? '*' : ''}</div></div>
                                            <input type="text" className={`t_from_input ${emailError ? 't_f_invalid' : ''}`} placeholder={ formLanguage?.Enter_Email ?? `Enter your email`} onBlur={this.onTextEmail} onChange={this.onTextEmail} value={email} />
                                            {emailError ? <div className='t_f_error'> {String(email).trim().length == 0 && ![179935].includes(wall?.Wall?.owner) ? "Email is required." : !validateEmail(email) ? "Enter valid email." : ""}</div> : null}
                                        </div>
                                    </div> : null}
                                </div>
                                 {[104746].includes(wall.Wall.owner) ?
                                    <div className='t_f_check_inline'>
                                        <input type="checkbox" id="t_accept" className='t_from_check' onChange={(e) => this.termsCheck(e.target.checked ? 1 : 0)} />
                                        <div className='t_f_label_wrap'>
                                            <label className='t_check_label_' htmlFor="t_accept">I agree to CALM using my story for marketing purposes --</label>
                                            <a href={isPhotoBooth ? wall.PhotoBoothSettings.photo_booth_terms_url : wall.UgcSettings.onsite_terms_url} className='t_tc_link' target='_blank'>click for more information on how your story will be used.</a>
                                        </div>
                                    </div>
                                    :
                                    <div className='t_f_group'>
                                        <div className='t_f_check_inline'>
                                            <input type="checkbox" id="t_accept" className='t_from_check' value={'accept'} onChange={this.onUpdatetandc} checked={tandc} />
                                            <label className='t_check_label_' htmlFor="t_accept">{formLanguage?.Agreeing ?? `By clicking on Submit, you are agreeing to our`} <a href={`${WebUrl}${isTagEmbed ? 'review-hub-content-rights' : isTagbox ? 'on-site-uploads-content-rights/' : 'on-site-uploads-content-rights/'}`} className='t_tc_link' target='_blank'><b>{formLanguage?.TnC ?? `Terms and conditions`}</b></a> *</label>
                                        </div>
                                        {tandc == false ? <div className="t_f_error" >* Please agree to the terms and conditions to submit your media.</div> : null}
                                    </div>
                                }
                                {submitError ?
                                    <div className='t_f_group'>
                                        <div className='t_f_error'>* {submitErrorMessage ? submitErrorMessage : `Please check complete details.`}</div>
                                    </div>
                                    : null}
                                <div className='t_f_group t_text_center'>
                                <div  
                                    className={`t_f_p_btn ${apiRequest ? ` t_f_btn_spinner` : ``}`}
                                    onClick={() => { this.onSubmitPostData(this.state.fileUpload); }}
                                    >
                                    { isPhotoBooth ? wall.PhotoBoothSettings.photo_booth_popup_btn_txt : wall.UgcSettings.onsite_popup_btn_txt}
                                    </div>
                                </div>
                                {step == 2 ? <div className='t_f_group t_text_center'>
                                    <div className={`t_f_s_btn`} onClick={this.backStepFun}>Back</div>
                                </div> : null}
                                {errorClickMore ? <div className='t_f_error'>* Max 20 posts are allowed.</div> : null}
                            </div>}
                    </div>
                    : null}
            </div>
        </div >

    }
}
const mapStateToProps = state => {


    if (state.modalPop != undefined && state.modalPop.onSiteIsShowPopUp == true) {
        document.querySelector('html').style.overflow = "hidden";
    }

    return {
        modalPop: state.modalPop
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onSitePopup: (status) => dispatch(onSitePopup(status)),
        onsiteTokenUpdate: (token) => dispatch(onsiteTokenUpdate(token)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ReviewHub);
